<template>
  <base-panel :title="carTitle" class="group-detail base-panel">
    <el-row>
      <el-col :span="18">
        <xdh-list
          :data="data"
          layout="horizontal"
          :column="6"
          :gutter="20"
          :split="false"
          justify="space-between"
        >
          <el-card
            class="box-card"
            :class="{ add: item.isAdd }"
            shadow="hover"
            slot-scope="{ item, index }"
            :body-style="{ padding: 0 }"
            @click.native.stop="handleClick(item)"
          >
            <div class="cardTitle">{{ item.title }}</div>
            <i class="el-icon-plus" v-if="item.isAdd"></i>
            <!-- {{item.dd}} -->

            <div class="action" v-if="!item.isAdd">
              <i class="el-icon-edit" @click.stop="handleEdit(item)"></i>
              <i
                class="el-icon-delete"
                @click.stop="handleRemove(item, index)"
              ></i>
            </div>
          </el-card>
        </xdh-list>
        <el-dialog
          :title="carTitle"
          :visible.sync="dialogVisible"
          width="500px"
        >
          <xdh-form
            ref="form"
            footer-align="right"
            label-width="80px"
            class="form"
            @submit="handleSubmit"
            :footer-border="false"
            :model="editItem"
          >
            <xdh-form-item
              prop="name"
              label="名称"
              :rules="{
                required: true,
                message: '请输入名称',
                trigger: 'blur'
              }"
            ></xdh-form-item>
            <xdh-form-item
              prop="title"
              label="标题"
              :rules="{
                required: true,
                message: '请输入标题',
                trigger: 'blur'
              }"
            ></xdh-form-item>
            <xdh-form-item
              prop="server"
              label="服务"
              :rules="{
                required: true,
                message: '请输入服务',
                trigger: 'blur'
              }"
            ></xdh-form-item>
            <xdh-form-item
              prop="path"
              label="地址"
              :rules="{
                required: true,
                message: '请输入地址',
                trigger: 'blur'
              }"
            ></xdh-form-item>
          </xdh-form>
        </el-dialog>
      </el-col>
      <el-col :span="6" class="right_form">
        <el-form ref="form" label-width="80px">
          <el-form-item label="模板名称">
            <el-input v-model="formObj.name"></el-input>
          </el-form-item>
          <el-form-item label="模板类型">
            <el-input v-model="formObj.type"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button
              @click="saveBtn()"
              class="el-button el-button--primary el-button--mini"
              >{{ btnText }}</el-button
            >
            <el-button @click="backBtn()">取消</el-button>
          </el-form-item>
        </el-form>
      </el-col>
    </el-row>
  </base-panel>
</template>
<script>
import XdhList from './components/card/xdh-list';
import XdhForm from './components/xdh-form';
import XdhFormItem from './components/xdh-form-item';
// import FormMixin from '@/base/mixin/forms'
import { LOCAL_STORAGE } from './components/config.js';
import {
  fetchForms,
  addForms,
  updateForms,
  removeForms
} from './components/storage';
import { mapState, mapMutations } from 'vuex';
import { cloneDeep, differenceWith } from 'lodash';
// import mixin from '../mixin';
import vue from 'vue';
import {
  applicationAdd,
  applicationEdit,
  applicationDetail,
  applicationOperationDetail
} from '@api/applicationManage.js';
export default {
  name: 'UserGroupDetail',
  components: {
    XdhList,
    XdhForm,
    XdhFormItem
  },
  data() {
    const _this = this;
    return {
      carTitle: '新增',
      dialogVisible: false,
      editItem: null,
      btnText: '新建',
      forms: {
        list: []
      },
      formObj: {
        name: '',
        type: 0
      },
      templetDetail: {}, // 模板详情
      operationDetail: [] // 磁贴详情
    };
  },
  computed: {
    data() {
      return [
        {
          id: 0,
          isAdd: true
        },
        ...this.forms.list
      ];
    }
  },
  watch: {},
  created() {
    this.refresh();
    this._btnText();
    if (this.$route.params.type === 'edit') {
      this.getApplicationDetail();
    }
    if (this.$route.params.type === 'add') {
      window.localStorage.clear();
      this.forms.list = [];
    }
    // this.formObj = JSON.parse(window.localStorage.getItem('formObj'));
    // alert(this.$route.params.type)
  },
  methods: {
    //新增模板
    temletAdd() {
      const params = {
        ...this.formObj,
        operation: this.forms.list
      };
      applicationAdd(params).then(({ error: er }) => {
        if (!er) {
          this.$message.success(`操作成功`);
          this.$router.push({ name: 'applicationManage' });
        } else {
          this.$message.error(er.message);
        }
        window.localStorage.clear();
      });
    },
    // 编辑模板
    _applicationEdit() {
      const opt = JSON.parse(window.localStorage.getItem('__forms__'));
      opt.forEach((item) => {
        if (Object.prototype.hasOwnProperty.call(item, 'operation')) {
          item.operation = 2;
        }
        if (!Object.prototype.hasOwnProperty.call(item, 'operation')) {
          item.operation = 1;
        }
      });
      const params = {
        ...this.formObj,
        operation: opt
      };
      applicationEdit(params).then(({ error: er }) => {
        if (!er) {
          this.$message.success(`操作成功`);
          this.$router.push({ name: 'applicationManage' });
        } else {
          this.$message.error(er.message);
        }
        window.localStorage.clear();
      });
    },
    // 模板详情
    getApplicationDetail() {
      applicationDetail(this.$route.params.id).then((res) => {
        this.templetDetail = res.data;
        this.operationDetail = res.data.operation;
        const form = {
          name: this.templetDetail.name,
          type: this.templetDetail.type
        };
        window.localStorage.setItem(
          '__forms__',
          JSON.stringify(this.operationDetail)
        );
        window.localStorage.setItem('formObj', JSON.stringify(form));
        this.formObj = this.templetDetail;
        this.forms.list = this.operationDetail;
      });
    },
    //新建或保存
    saveBtn() {
      if (this.$route.params.type === 'add') {
        this.temletAdd();
      }
      if (this.$route.params.type === 'edit') {
        this._applicationEdit();
      }
    },
    // 取消
    backBtn() {
      this.$router.push({ name: 'applicationManage' });
    },
    //切换新建或修改的按钮名称
    _btnText() {
      this.btnText = this.$route.params.type === 'add' ? '新建' : '保存';
    },
    refresh() {
      if (LOCAL_STORAGE) {
        this.forms.list = fetchForms();
      } else {
        this.fetchForms();
      }
    },
    handleEdit(item) {
      this.carTitle = '编辑磁贴';
      this.editItem = item;
      this.dialogVisible = true;
    },
    handleRemove(item, index) {
      this.$confirm('确认删除？', '提示').then((r) => {
        if (LOCAL_STORAGE) {
          removeForms(item.id);
          this.refresh();
        } else {
          this.removeForms(item.id, null, index - 1);
        }
      });
    },
    handleClick(item) {
      if (item.isAdd) {
        this.editItem = null;
        this.carTitle = '新建磁贴';
        this.dialogVisible = true;
      }
    },
    handleSubmit(model) {
      if (this.editItem) {
        if (LOCAL_STORAGE) {
          updateForms(model.id, model);
          this.editItem = null;
          this.$refs.form.reset();
          this.dialogVisible = false;
          this.refresh();
        } else {
          this.updateForms(model).then((r) => {
            this.editItem = null;
            this.$refs.form.reset();
            this.dialogVisible = false;
            this.refresh();
          });
        }
      } else {
        if (LOCAL_STORAGE) {
          addForms(model);
          this.$refs.form.reset();
          this.dialogVisible = false;
          this.refresh();
        } else {
          this.addForms(model).then((r) => {
            this.$refs.form.reset();
            this.dialogVisible = false;
            this.refresh();
          });
        }
      }
    }
  }
};
</script>
<style lang="scss" scoped>
@import '~@assets/scss/theme.scss';
.right_form {
  padding: 20px 10px;
}
.el-button--primary {
  background-color: #266ab9;
  border-color: #266ab9;
}
.cardTitle {
  position: relative;
  top: 0px;
  left: 15px;
  text-align: left;
}
.list-wrapper {
  padding: 20px;
}

.form {
  margin: 15px;
}

.box-card {
  height: 156px;
  min-width: 120px;
  line-height: 55px;
  text-align: center;
  font-size: 16px;
  margin-top: 20px;
  // background: $--color-primary-light-9;
  // color: $--color-text-primary;
  position: relative;
  cursor: pointer;

  &.add {
    line-height: 166px;
    // color: $--color-primary;
  }

  .el-icon-plus {
    font-size: 38px;
    font-weight: bold;
  }
}

.action {
  line-height: 40px;
  position: absolute;
  left: 0;
  bottom: 0;
  height: 40px;
  font-size: 20px;
  text-align: right;
  width: 100%;

  i {
    padding: 0 10px;
    cursor: pointer;
    height: 35px;
    line-height: 35px;
    // border-radius: $--border-radius-base;
    margin-right: 5px;

    &:hover {
      // background: $--color-primary-light-8;
    }
  }
}
</style>
